[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  @include vertical;
  @include gpu;
  overflow: auto;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
  background-color: rgba($black, 0.7);
  padding: 15px; }

[data-modal] {
  &.is-open {
    opacity: 1;
    visibility: visible;
    z-index: 100; } }

[data-modal-container] {
  position: relative;
  z-index: 1;
  vertical-align: middle;
  display: inline-block;
  max-width: 1000px;
  margin: 0 auto; }

[data-modal-close] {
  position: absolute;
  background-image: $redGrad;
  @include circle(40);
  // background-color: $blue
  font-size: 0;
  right: 40px;
  top: 42px;
  transition: opacity $tr;
  @include sm {
    @include circle(20);
    right: 16px;
    top: 16px; }
  @include hover {
    opacity: 0.8; }
  .icon {
    @include vcenter;
    font-size: 18px;
    color: $white;
    @include sm {
      font-size: 10px; } } }

.modal {
  &__container {
    background-color: $white;
    padding: 120px 120px 135px;
    @include sm {
      padding: 40px 15px; } }
  &__title {
    text-align: left;
    margin-bottom: 44px;
    @include sm {
      margin-bottom: 20px; } }
  &__text {
    color: rgba($mainTextColor, 0.5);
    font-family: $montserrat;
    font-size: 12px;
    font-weight: 600;
    line-height: 1.883;
    @include sm {
      line-height: 1.833; } } }

