.footer {
  padding: 70px 0;
  background-color: $mainTextColor;
  @include md {
    padding: 50px 0; }
  @include sm {
    padding: 50px 0 30px; }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include sm {
      flex-direction: column; } }
  &__details {
    @include sm {
      margin: 18px auto 33px; } } }

.details {
  @include flex-center;
  margin: 0 -12px;
  &__item {
    padding: 0 12px; }
  &__link {
    opacity: 0.5;
    color: $white;
    font-family: $montserrat;
    font-size: 12px;
    font-weight: 500;
    line-height: 2;
    @include hover {
      span:after {
        transform: scaleX(1);
        transform-origin: 0 0; } }
    span {
      line-height: 1.2;
      display: inline-block;
      vertical-align: middle;
      position: relative;
      overflow: hidden;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 1px;
        width: 100%;
        background-color: $white;
        transform: scaleX(0);
        transition: transform .4s;
        transform-origin: 100% 0; } } } }

.mastercard {
  img {
    display: block;
    max-width: 100%; } }
