.details__link span {
  line-height: 1.2;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  overflow: hidden;
}

.details__link span:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: #fff;
  transform: scaleX(0);
  transition: transform .4s;
  transform-origin: 100% 0;
}

.mastercard img {
  display: block;
  max-width: 100%;
}

.cookies {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 730px;
  z-index: 40;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 34px 30px 34px 50px;
  box-shadow: 0 -42px 84px -20px rgba(0, 0, 0, 0.5);
  background-image: linear-gradient(262deg, #f15f4b 0%, #874299 100%);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s, visibility 0.4s;
}

@media only screen and (max-width: 767px) {
  .cookies {
    flex-direction: column;
  }
}

.cookies.is-open {
  opacity: 1;
  visibility: visible;
}

.cookies__descr {
  margin-right: 77px;
  color: #fff;
}

@media only screen and (max-width: 767px) {
  .cookies__descr {
    margin-right: 0;
    margin-bottom: 24px;
  }
}

.btn {
  display: inline-block;
  padding: 12px 67px;
  color: #231f20;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.3;
  box-shadow: 0 42px 34px -20px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  background-color: #fff;
  transition: box-shadow 0.4s;
}

[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  text-align: center;
  font-size: 0;
  transform: translate3d(0, 0, 0);
  overflow: auto;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 15px;
}

[data-modal]:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

[data-modal-container] {
  position: relative;
  z-index: 1;
  vertical-align: middle;
  display: inline-block;
  max-width: 1000px;
  margin: 0 auto;
}

[data-modal-close] {
  position: absolute;
  background-image: linear-gradient(-30deg, #f15f4b 0%, #874299 100%);
  height: 40px;
  width: 40px;
  border-radius: 50%;
  font-size: 0;
  right: 40px;
  top: 42px;
  transition: opacity 0.4s;
}

@media only screen and (max-width: 767px) {
  [data-modal-close] {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    right: 16px;
    top: 16px;
  }
}

.no-touch [data-modal-close]:hover {
  opacity: 0.8;
}

[data-modal-close] .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 18px;
  color: #fff;
}

@media only screen and (max-width: 767px) {
  [data-modal-close] .icon {
    font-size: 10px;
  }
}

.modal__container {
  background-color: #fff;
  padding: 120px 120px 135px;
}

@media only screen and (max-width: 767px) {
  .modal__container {
    padding: 40px 15px;
  }
}

.modal__title {
  text-align: left;
  margin-bottom: 44px;
}

@media only screen and (max-width: 767px) {
  .modal__title {
    margin-bottom: 20px;
  }
}

.modal__text {
  color: rgba(35, 31, 32, 0.5);
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.883;
}

@media only screen and (max-width: 767px) {
  .modal__text {
    line-height: 1.833;
  }
}
