.solutions {
  margin-bottom: 18px;
  &__wrapper {
    background-color: $white;
    text-align: center;
    padding-top: 51px;
    padding-bottom: 36px;
    @include sm {
      padding: 50px 21px 36px; } }
  // &__title
  //   margin-bottom: 10px
  &__btn {
    @include flex-center;
    margin-top: 27px;
    @include sm {
      margin-top: 47px; } } }

.solutions-cta {
  position: relative;
  padding: 18px;
  &__img {
    display: block; }
  &__arrow {
    position: absolute;
    transform-origin: center;
    font-size: 0;
    // opacity: 0
    // transition: transform 0.6s ease-out
    .is-animate & {
 }      // opacity: 1
    &:nth-child(1) {
      top: 0;
      left: 0;
      transform: translate(-7px, -10px);
      .is-animate & {
        animation: solutionsBtnTopLeftArrowAnimation ease-in-out 1.2s infinite alternate; } }
    &:nth-child(2) {
      top: 0;
      right: 0;
      transform: translate(7px, -10px) rotate(90deg);
      .is-animate & {
        animation: solutionsBtnTopRightArrowAnimation ease-in-out 1.2s infinite alternate; } }
    &:nth-child(3) {
      bottom: 0;
      right: 0;
      transform: translate(7px,17px) rotate(180deg);
      .is-animate & {
        animation: solutionsBtnBottomRightArrowAnimation ease-in-out 1.2s infinite alternate; } }
    &:nth-child(4) {
      bottom: 0;
      left: 0;
      transform: translate(-7px, 17px) rotate(-90deg);
      .is-animate & {
        animation: solutionsBtnBottomLeftArrowAnimation ease-in-out 1.2s infinite alternate; } }
    .icon {
      font-size: 22px;
      color: #e7e7e7; } } }

@keyframes solutionsBtnTopLeftArrowAnimation {
  to {
    transform: translate(0, 0); } }

@keyframes solutionsBtnTopRightArrowAnimation {
  to {
    transform: translate(0, 0) rotate(90deg); } }

@keyframes solutionsBtnBottomRightArrowAnimation {
  to {
    transform: translate(0, 0) rotate(180deg); } }

@keyframes solutionsBtnBottomLeftArrowAnimation {
  to {
    transform: translate(0, 0) rotate(-90deg); } }
