.wearables {
  @include sm {
    text-align: center; }
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    min-height: 447px;
    background-color: $white;
    @include sm {
      min-height: inherit; } }
  &__images {
    position: absolute !important;
    right: 0;
    top: 0;
    left: 35.9%;
    height: 100%;
    min-height: 400px;
    max-width: 64.1%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include sm {
      position: relative !important;
      left: 0;
      height: auto;
      max-width: initial;
      min-height: 350px; } }
  &__image {
    padding: 30px;
    @include coverdiv;
    display: flex;
    align-items: center;
    justify-content: center;
    @include sm {
      padding: 15px 35px; }
    img {
      display: block;
      max-width: 320px;
      width: 100%;
      max-height: 340px;
      height: auto;
      margin-top: 22px; } }
  &__texts,
  &__tabs,
  &__info-wrapper {
    width: 35.9%;
    @include sm {
      width: 100%; } }
  &__texts {
    display: flex;
    margin-bottom: 8px; }
  &__text,
  &__text[data-tabs-container].is-open {
    display: block;
    padding: 40px 65px 10px 33px;
    @include sm {
      padding: 28px 20px 10px; }
    .title {
      margin-bottom: 7px; } }
  &__tabs {
    padding-left: 33px;
    padding-bottom: 12px;
    padding-top: 12px;
    margin-bottom: auto;
    @include sm {
      padding-left: 0;
      padding-right: 0; } }
  &__info-wrapper {
    background-image: $grad;
    @include hover {
      .battery__line {
        &:after {
          animation:  batteryLine 0.6s forwards ease-in-out; } } }
    @include sm {
      max-height: 83px; } }
  &__info {
    padding: 44px 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include sm {
      padding: 30px 20px; }
    span {
      color: $white;
      @include bigText;
      display: inline-block;
      vertical-align: middle; }
    .icon {
      font-size: 23px;
      color: $white;
      display: inline-block;
      vertical-align: middle; } } }

.battery {
  position: relative;
  @include hover {
    .battery__line {
      &:after {
        animation:  batteryLine 0.6s forwards ease-in-out; } } }
  &__line {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 140%;
    width: 2px;
    overflow: hidden;
    transform: translate(-50%, -50%) rotate(46deg);
    transform-origin: center;
    &:after {
      content: '';
      @include coverdiv;
      background-color: $white;
      // transform: translateY(-101%)
      transition: transform 0.4s; } } }

@keyframes batteryLine {
  0% {
    transform: translateY(0); }
  49% {
    transform: translateY(-110%);
    opacity: 1; }
  50% {
    opacity: 0; }
  51% {
    transform: translateY(110%);
    opacity: 0; }
  51.1% {
    opacity: 1; }
  100% {
    transform: translateY(0); } }
