.about {
  margin-bottom: 50px;
  @include sm {
    margin-bottom: 20px; }
  &__wrapper {
    position: relative;
    @include clr; }
  &__image-wrapper {
    position: absolute;
    right: 0;
    top: 0;
    width: 58.4%;
    @include sm {
      position: static;
      width: 100%;
      padding: 0 14px; } }
  &__text-wrapper {
    position: relative;
    float: left;
    margin-top: 60px;
    width: 53.5%;
    z-index: 5;
    @include sm {
      float: none;
      width: 100%;
      margin-top: -28px; } }
  .text-card__inner {
    padding-top: 10px; } }

