.payments {
  margin-bottom: 22px;
  &__wrapper {
    padding: 50px 0;
    @include sm {
      padding: 40px 17px 20px; } }
  &__title {
    @include sm {
      margin-bottom: 22px; } }
  &__logos {
    @include flex-center;
    flex-wrap: wrap;
    margin: 24px -20px 0;
    @include sm {
      margin: 36px auto 0; } }
  &__logo {
    padding: 0 20px;
    position: relative;
    @include sm {
      margin-bottom: 31px;
      padding: 0 10px; }
    img {
      max-height: 50px;
      display: block; } } }
