.luxury {
  margin-bottom: 20px;
  &__wrapper {
    position: relative;
    padding-top: 114px;
    padding-bottom: 70px;
    @include sm {
      padding: 130px 24px 32px; } }
  &__images {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-left: -6px;
    position: absolute;
    top: -140px;
    left: 0;
    width: 100%;
    animation: soar 3.5s ease-in-out infinite;
    backface-visibility: hidden;
    @include md {
      margin-left: 6px; }
    @include sm {
      top: -60px;
      margin-left: 0; } }
  &__main-image {
    max-width: 540px;
    @include sm {
      max-width: 300px;
      margin-right: -10px; } }
  &__circle {
    max-width: 200px;
    @include md {
      display: none; }
    &_left {
      margin-top: 38px;
      margin-right: -35px; }
    &_right {
      margin-top: 32px;
      margin-left: -75px; } }
  &__title {
    margin-bottom: 19px; } }

@keyframes soar {
  0%, 100% {
    transform: translate3d(0,0,0); }
  50% {
    transform: translate3d(0,-10px,0); } }

@keyframes fadeShadow {
  0%, 100% {
    opacity: 1; }
  50% {
    opacity: 0.2; } }
