html.is-overflow-hidden {
  overflow-x: hidden;
  overflow-y: scroll;
  body {
    overflow: hidden;
    height: 100vh; } }


body {
  position: relative;
  background: $light;
  color: rgba($mainTextColor, 0.5);
  font-family: $montserrat;
  font-size: 13px;
  font-weight: 600;
  line-height: 1.667;
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
  opacity: 0;
  visibility: hidden;
  @include smooth-font;
  &.is-loaded {
    opacity: 1;
    visibility: visible; }
  @include sm {
    line-height: 1.833; } }
a {
  text-decoration: none;
  color: inherit; }

.out {
  overflow: hidden; }

.text-card,
.image-card {
  width: 100%;
  @include flex-center;
  min-height: 391px;
  @include md {
    min-height: 350px; } }

.text-card {
  background-color: $white;
  text-align: center;
  box-shadow: 0 22px 44px -11px rgba($black, 0.16);
  padding: 20px 70px;
  @include md {
    padding: 20px 35px; }
  @include sm {
    min-height: inherit;
    line-height: 1.667;
    padding: 27px 35px 50px; }
  &__title {
    margin-bottom: 10px; }
  &__form {
    padding-bottom: 10px;
    @include sm {
      padding-top: 45px;
      padding-bottom: 30px; } } }

.image-card {
  padding: 10px;
  text-align: center;
  background-color: $white;
  box-shadow: 0 12px 14px -10px rgba($black, 0.11);
  align-items: stretch;
  @include sm {
    min-height: 210px;
    box-shadow: 0 6px 7px -5px rgba($black, 0.11); }
  &_logo {
    background-image: $grad;
    box-shadow: none;
    align-items: center;
    @include sm {
      min-height: 165px; } }
  &__text {
    color: $white;
    font-size: 32px;
    letter-spacing: 0.019em;
    line-height: 1;
    @include sm {
      font-size: 20px; } }
  &__logo {
    margin-bottom: 18px;
    @include sm {
      margin-bottom: 9px; }
    svg {
      height: 69px;
      width: auto;
      fill: $white;
      @include sm {
        height: 44px; } } }
  &__image {
    width: 100%;
    @include cover; } }

.section-title {
  margin-bottom: 10px; }

